"use client"

import type React from "react"

import { createClient } from "@/lib/supabase/client"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { useState } from "react"
import { AlertCircle, Mail } from "lucide-react"

export default function Login() {
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [error, setError] = useState<string | null>(null)
  const [isLoading, setIsLoading] = useState(false)
  const [showForgotPassword, setShowForgotPassword] = useState(false)
  const [forgotEmail, setForgotEmail] = useState("")
  const [forgotLoading, setForgotLoading] = useState(false)
  const [resetSent, setResetSent] = useState(false)
  const router = useRouter()

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    const supabase = createClient()
    setIsLoading(true)
    setError(null)

    try {
      console.log("[v0] Tentando login com email:", email)
      const { error: loginError } = await supabase.auth.signInWithPassword({
        email,
        password,
      })
      if (loginError) {
        console.log("[v0] Erro de login:", loginError.message)
        throw loginError
      }
      console.log("[v0] Login bem-sucedido")
      router.push("/dashboard")
    } catch (error: unknown) {
      const errorMsg = error instanceof Error ? error.message : "Erro ao fazer login"
      console.log("[v0] Erro capturado:", errorMsg)
      // Melhorar mensagem de erro
      if (errorMsg.includes("Invalid login credentials")) {
        setError("Email ou senha incorretos. Verifique se sua conta foi confirmada.")
      } else if (errorMsg.includes("Email not confirmed")) {
        setError("Por favor, confirme seu email antes de fazer login. Verifique sua caixa de entrada.")
      } else {
        setError(errorMsg)
      }
    } finally {
      setIsLoading(false)
    }
  }

  const handleForgotPassword = async (e: React.FormEvent) => {
    e.preventDefault()
    const supabase = createClient()
    setForgotLoading(true)
    setError(null)

    try {
      console.log("[v0] Enviando link de recuperação para:", forgotEmail)
      const { error: resetError } = await supabase.auth.resetPasswordForEmail(forgotEmail, {
        redirectTo: `${window.location.origin}/auth/reset-password`,
      })
      if (resetError) throw resetError
      console.log("[v0] Email de recuperação enviado")
      setResetSent(true)
      setTimeout(() => {
        setShowForgotPassword(false)
        setResetSent(false)
        setForgotEmail("")
      }, 3000)
    } catch (error: unknown) {
      setError(error instanceof Error ? error.message : "Erro ao enviar email de recuperação")
    } finally {
      setForgotLoading(false)
    }
  }

  return (
    <div className="flex min-h-screen w-full items-center justify-center p-4 sm:p-6 md:p-10">
      <div className="w-full max-w-sm">
        <Card className="border-slate-700 bg-slate-900">
          <CardHeader>
            <CardTitle className="text-2xl text-white">Bem-vindo</CardTitle>
            <CardDescription className="text-slate-400">Faça login para acessar suas oportunidades</CardDescription>
          </CardHeader>
          <CardContent>
            {!showForgotPassword ? (
              <form onSubmit={handleLogin} className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="email" className="text-slate-200">
                    Email
                  </Label>
                  <Input
                    id="email"
                    type="email"
                    placeholder="seu@email.com"
                    required
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    className="border-slate-600 bg-slate-800 text-white placeholder-slate-500 focus-visible:ring-blue-500"
                  />
                </div>

                <div className="space-y-2">
                  <div className="flex items-center justify-between">
                    <Label htmlFor="password" className="text-slate-200">
                      Senha
                    </Label>
                    <button
                      type="button"
                      onClick={() => setShowForgotPassword(true)}
                      className="text-xs text-blue-400 hover:text-blue-300 underline"
                    >
                      Esqueci a senha
                    </button>
                  </div>
                  <Input
                    id="password"
                    type="password"
                    required
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    className="border-slate-600 bg-slate-800 text-white placeholder-slate-500 focus-visible:ring-blue-500"
                  />
                </div>

                {error && (
                  <div className="flex gap-2 rounded-lg bg-red-500/10 p-3 text-sm text-red-400">
                    <AlertCircle className="h-4 w-4 shrink-0 mt-0.5" />
                    {error}
                  </div>
                )}

                <Button
                  type="submit"
                  className="w-full bg-gradient-to-r from-blue-500 to-emerald-500 text-white hover:from-blue-600 hover:to-emerald-600"
                  disabled={isLoading}
                >
                  {isLoading ? "Entrando..." : "Fazer Login"}
                </Button>

                <div className="text-center text-sm text-slate-400">
                  Não tem uma conta?{" "}
                  <Link href="/auth/sign-up" className="text-blue-400 underline underline-offset-4 hover:text-blue-300">
                    Criar conta
                  </Link>
                </div>
              </form>
            ) : (
              <form onSubmit={handleForgotPassword} className="space-y-4">
                <div className="flex items-center justify-center w-12 h-12 rounded-full bg-blue-500/20 mx-auto mb-4">
                  <Mail className="w-6 h-6 text-blue-400" />
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-white text-center mb-2">Recuperar Senha</h3>
                  <p className="text-sm text-slate-400 text-center mb-4">
                    Digite seu email e enviaremos um link para redefinir sua senha
                  </p>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="forgotEmail" className="text-slate-200">
                    Email
                  </Label>
                  <Input
                    id="forgotEmail"
                    type="email"
                    placeholder="seu@email.com"
                    required
                    value={forgotEmail}
                    onChange={(e) => setForgotEmail(e.target.value)}
                    className="border-slate-600 bg-slate-800 text-white placeholder-slate-500 focus-visible:ring-blue-500"
                  />
                </div>

                {error && (
                  <div className="flex gap-2 rounded-lg bg-red-500/10 p-3 text-sm text-red-400">
                    <AlertCircle className="h-4 w-4 shrink-0 mt-0.5" />
                    {error}
                  </div>
                )}

                {resetSent && (
                  <div className="flex gap-2 rounded-lg bg-green-500/10 p-3 text-sm text-green-400">
                    <Mail className="h-4 w-4 shrink-0 mt-0.5" />
                    Email enviado com sucesso! Verifique sua caixa de entrada.
                  </div>
                )}

                <Button
                  type="submit"
                  className="w-full bg-gradient-to-r from-blue-500 to-emerald-500 text-white hover:from-blue-600 hover:to-emerald-600"
                  disabled={forgotLoading}
                >
                  {forgotLoading ? "Enviando..." : "Enviar Link de Recuperação"}
                </Button>

                <button
                  type="button"
                  onClick={() => setShowForgotPassword(false)}
                  className="w-full text-sm text-slate-400 hover:text-slate-300 py-2"
                >
                  Voltar ao login
                </button>
              </form>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
